import React, { useState } from "react";
import { AnimatePresence, motion } from "framer-motion";
import { Switch, Route, NavLink, useLocation } from "react-router-dom";
import MainContent from "./Components/MainContent";
import Resume from "./Components/Resume";

import FavoriteProjects from "./Components/FavoriteProjects";
import "./Assets/css/mainAppStyles.css";

function App() {
  const location = useLocation();
  const [navBarToggle, setNavBarToggle] = useState("150px");
  const [navBarToggleDIM, setNavBarToggleDIM] = useState(0.3);
  const [navBarToggleButtonColor, setNavBarToggleButtonColor] = useState(
    "white"
  );
  // const [
  //   navBarToggleButtonBackColor,
  //   setNavBarToggleButtonBackColor,
  // ] = useState("black");

  const [hideNavBarButtonToggle, setHideNavBarButtonToggle] = useState(
    "inline"
  );

  function toggleNavBar() {
    if (navBarToggle === 0) {
      setNavBarToggle("150px");
      setHideNavBarButtonToggle("inline");
      setNavBarToggleButtonColor("white");
      setNavBarToggleDIM(0.3);

      // setNavBarToggleButtonBackColor("black");
    } else {
      setNavBarToggle(0);
      setHideNavBarButtonToggle("none");
      setNavBarToggleButtonColor("black");
      setNavBarToggleDIM(1);

      // setNavBarToggleButtonBackColor("white");
    }
  }
  return (
    <div class="outer-main-div">
      <motion.span
        initial={{ opacity: 0, y: "-100px" }}
        animate={{ opacity: 1, y: "0px" }}
        transition={{ duration: 1.5, ease: "easeIn" }}
        style={{
          position: "fixed",
          left: 0,
          top: 0,
          zIndex: 3,
          margin: 10,
          color: navBarToggleButtonColor,
          textAlign: "center",
          border: "none",
          fontWeight: 900,
          padding: 10,
          cursor: "pointer",
        }}
        onClick={toggleNavBar}
      >
        &#9776;
      </motion.span>

      <div style={{ backgroundColor: "blue" }}>
        <motion.nav
          initial={{ opacity: 0, x: "-100px" }}
          animate={{ opacity: 1, x: "0px" }}
          className="side-main-navbar"
          style={{
            opacity: navBarToggleDIM,
          }}
          transition={{ property: "opacity", duration: 2, ease: "anticipate" }}
        >
          <motion.ul
            initial={{ opacity: 0, y: "-100px" }}
            animate={{ opacity: 1, y: "0px" }}
            transition={{ duration: 3, ease: "anticipate" }}
            style={{
              display: "flex",
              flexDirection: "column",
              marginTop: 70,
            }}
          >
            <li
              className="navbar-link"
              style={{
                display: hideNavBarButtonToggle,
              }}
            >
              <NavLink to="/" exact>
                About
              </NavLink>
            </li>

            <li
              className="navbar-link"
              style={{ display: hideNavBarButtonToggle }}
            >
              <NavLink to="/about">Projects</NavLink>
            </li>

            <li
              className="navbar-link"
              style={{ display: hideNavBarButtonToggle }}
            >
              <NavLink to="/settings">CV</NavLink>
            </li>
          </motion.ul>
        </motion.nav>

        <main>
          <div
            className="main-tag"
            style={{
              position: "absolute",
              left: navBarToggle,
              right: 0,
              top: 0,
              bottom: 0,
              opacity: navBarToggleDIM,
              transition: "2s",
              overflowX: "hidden",
            }}
          >
            <AnimatePresence>
              <Switch location={location} key={location.pathname}>
                <Route path="/about" component={FavoriteProjects} />
                <Route path="/" exact component={MainContent} />
                <Route path="/settings" component={Resume} />
              </Switch>
            </AnimatePresence>
          </div>
        </main>
      </div>
    </div>
  );
}

export default App;
